@import url('https://fonts.googleapis.com/css?family=Lato:300,400|Oswald:300,700&display=swap');

@tailwind base;
@tailwind components;

.light-shadow {
    box-shadow: 0 5px 5px 0 rgba(233,240,243,0.5), 0 0 0 1px #E6ECF8;
}

.border-light {
    border-color: #E6ECF8;
}

@responsive {
    .gray-gradient {
        background-image: linear-gradient(to bottom, theme('colors.gray.100'), theme('colors.gray.400'));
    }

    .indigo-gradient {
        background-image: linear-gradient(to bottom, theme('colors.indigo.200'), theme('colors.indigo.400'));
    }
}

@tailwind utilities;
